<template>

    <div :style="style">
        <div style="margin-bottom: 25px">
            <i class="el-icon-plus"></i><span>添加</span>
            <div style="margin-top:10px;">
                <el-button type="primary" @click="addCourse()" plain>添加</el-button>
            </div>
        </div>
        <div>
            <div>
                <i class="el-icon-search"></i><span>查询</span>
                <div class='searchContent' style="margin-top:10px;">
                    <el-form :model="searchData" inline>
                        <el-form-item label="学院号:">
                            <el-input v-model.trim="searchData.departmentId"></el-input>
                        </el-form-item>
                        <el-form-item label="学院名:">
                            <el-input v-model.trim="searchData.departmentName"></el-input>
                        </el-form-item>

                        <el-form-item>
                            <el-button type="primary" @click="search" plain>查询</el-button>
                            <el-button type="success" @click="examineAll" plain>重置</el-button>
                        </el-form-item>
                    </el-form>
                </div>
            </div>
            <div>
                <div>
                    <i class="el-icon-document"><span>学院列表</span></i>
                </div>
            </div>

        </div>
        <el-table
                :data="departmentList"
                style="width: 100%"
                height="100%">
            <el-table-column
                    fixed
                    type="expand"
            >
                <template slot-scope="props">
                    <el-table
                            :data="props.row.children"
                            style="width: 100%;"
                            border
                            height="100%">
                        <el-table-column
                                fixed
                                prop="majorId"
                                label="专业号"
                        >
                        </el-table-column>
                        <el-table-column
                                fixed
                                prop="majorName"
                                label="专业名称"
                        >
                        </el-table-column>
                    </el-table>
                </template>
            </el-table-column>
            <el-table-column
                    fixed
                    prop="departmentId"
                    label="学院号"
            >
            </el-table-column>
            <el-table-column
                    fixed
                    prop="departmentName"
                    label="学院名"
            >
            </el-table-column>

        </el-table>
        <transition name="fade" mode="out-in">
            <el-dialog title="添加学院" :visible.sync="dialogFormVisible"
                       v-loading="loading"
                       element-loading-text="拼命加载中"
                       element-loading-spinner="el-icon-loading"
                       element-loading-background="rgba(0, 0, 0, 0.8)">
                <el-form :model="department" >
                    <el-form-item label="学院号" :label-width="formLabelWidth">
                        <el-input v-model="department.departmentId" autocomplete="on" size="normal"></el-input>
                    </el-form-item>
                    <el-form-item label="学院名" :label-width="formLabelWidth">
                        <el-input v-model="department.departmentName" autocomplete="on" size="normal"></el-input>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogFormVisible = false">取 消</el-button>
                    <el-button type="primary" @click="commitMessage()">确 定</el-button>
                </div>
            </el-dialog>
        </transition>
    </div>

</template>

<script>
export default {
  name: 'page',
  data () {
    return {
      loading: false,

      style: {
        height: window.innerHeight + 'px'
      },
      dialogFormVisible: false,
      formLabelWidth: '120px',
      departmentList: [
        {
          departmentName: '计算机',
          departmentId: '02', // 专业ID
          children: []
        },
        {
          departmentName: '音乐',
          departmentId: '02' // 专业ID
        }
      ],

      department: {
        departmentId: '',
        departmentName: ''

      },
      searchData: { // 搜索的数据
        departmentId: '',
        departmentName: ''
      }
    }
  },
  methods: {

    /**
             * 添加学生
             */
    addCourse () {
      this.dialogFormVisible = true
      this.department = {}
    },
    /**
             * 提交参数
             */
    commitMessage () {
      this.loading = true
      let data = this.department

      let callback = (res) => {
        console.log(res)
        this.loading = false
        this.dialogFormVisible = false
        this._notify('成功', 'success', '添加成功')
      }
      let error = (res) => {
        console.log(res)
        this.loading = false
        this.dialogFormVisible = false
        this._notify('警告', 'warning', '添加失败')
      }
      let exception = (res) => {
        console.log(res)
        this.loading = false
        this.dialogFormVisible = false
        this._notify('错误', 'error', '请求错误')
      }
      this._ajax('post', '/department/management/add', callback, {}, error, exception, data)
    },
    /**
             * 搜索学生
             */
    search () {
      if (this.searchData.departmentId === '' && this.searchData.departmentName === '') {
        this.getDepartmentList()
      } else {
        let params = this.searchData
        let callback = (res) => {
          console.log(res)
          this.departmentList = res.charData
        }
        let error = (res) => {
          console.log(res)
        }
        this._ajax('post', '/department/management/search', callback, {}, error, () => {}, params)
      }
    },
    /**
             * 重置查询内容
             */
    examineAll () {
      this.searchData = {}
    },
    getDepartmentList () {
      let callback = (res) => {
        this.departmentList = res.charData
        this.departmentList.sort((itemA, itemB) => {
          return parseInt(itemA.departmentId) - parseInt(itemB.departmentId)
        })
      }
      let error = (res) => {
        console.log(res)
      }
      let param = this.pageInfo
      this._ajax('get', '/department/management/list', callback, param, error, () => {})
    }
  },
  mounted () {
    console.log(this.platform)
    this.getDepartmentList()
  }
}
</script>

<style scoped>
    .btnFun {
        text-align: center;
        margin: 50px;
    }
    .bounce-enter-active {
        animation: bounce-in .5s;
    }
    .bounce-leave-active {
        animation: bounce-in .5s reverse;
    }
    @keyframes bounce-in {
        0% {
            transform: scale(0);
        }
        50% {
            transform: scale(1.5);
        }
        100% {
            transform: scale(1);
        }
    }
</style>



